::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: grey;
}
.detail-wrapper {
  display: flex;
  padding-top: 15vh;
  padding-bottom: 10vh;
  min-height: 100vh;
  justify-content: center;
  position: relative;
  width: 100%;
}
.detail-container {
  width: 90%;
}
.detail-block {
  width: 100%;
  border-radius: 10px;
  padding: 10px;
  padding-bottom: 5vh;
  margin: 0 0 5vh 0;
}
.detail-block-bl {
  background: #191919;
  color: whitesmoke;
}
.detail-block-lt {
  background: #f8f8f8;
  color: black;
}
.detail-img {
  border-radius: 10px;
  max-width: 100%;
}
.header-display {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
  overflow-x: scroll;
}
.icon-detail {
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.star {
  fill: #ffc107;
}
.heart:hover {
  fill: red;
  color: red;
}
.liked {
  color: red;
  fill: red;
}
.star-value {
  font-size: 100%;
  margin-left: 5px;
}
.eye {
  fill: grey;
}
.detail-header {
  width: 100%;
  padding: 10px 0 10px 0;

  white-space: nowrap;
  text-overflow: ellipsis;
}
.detail-genres {
  width: 90%;
  margin-bottom: 5vh;
  overflow-x: scroll;
  white-space: nowrap;
}
.description {
  height: 15vh;
  overflow-y: scroll;
}
.read {
  width: 100%;
  margin-top: 10px;
}
.btn-block {
  width: 100%;
}
.detail-main {
  width: 100%;
}
.detail-nav {
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.04);
  word-spacing: normal;
  overflow-x: scroll;
}
.detail-description {
  padding-left: 10px;
}
.nav-btn {
  cursor: pointer;
  padding: 10px 1.5rem 5px 10px;
  font-size: 1.8rem;
}
.active-btn {
  color: aqua;
}
.chapter-block {
  padding-top: 5vh;
}
.chapter-item {
  display: flex;
  width: 90%;
  padding-top: 10px;
  padding-left: 15px;
  padding-bottom: 10px;
  padding-right: 15px;
  border-radius: 10px;
  cursor: pointer;
  margin-bottom: 10px;
}
.chapter-item:hover {
  box-shadow: 0 0 10px black;
}

.pages-wrapper {
  width: 100%;
  opacity: 0.95;
  min-height: 50vh;
  background: black;
  display: flex;
  justify-content: center;
  padding-top: 15vh;
}
.set-star {
  display: flex;
  justify-content: space-between;
  padding: 10px 5px 10px 5px;
}
.set-comment-value {
  width: 100%;
}
.chosenStar {
  fill: red;
}
.review-item {
  display: flex;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 0.5rem;
}
.review-img-wrapper {
  width: 50px;
  height: 50px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 10px;
}
.review-img {
  display: inline;
  margin: 0 auto;
  margin-left: -25%;
  height: 100%;
  width: auto;
}
.review-text {
  width: 100%;
}

.rating {
  display: flex;
  font-size: 20px;
  line-height: 0.75;
  margin-bottom: 10px;
}
.rating__body {
  position: relative;
}
.rating__body::before {
  content: "★★★★★";
  display: block;
}
.rating__active {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  height: 100%;
}
.rating__active::before {
  content: "★★★★★";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #ffd300;
}
.rating__items {
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.rating__item {
  flex: 0 0 20%;
  height: 100%;
  opacity: 0;
}
.comment-title:hover {
  text-decoration: underline;
  color: aqua;
}
@media only screen and (min-width: 600px) {
  .review-item {
    width: 80%;
  }
}
